class Accordion {
  private static readonly AccordionTriggerSelector = ".js-accordion-trigger";
  private static readonly AccordionBodySelector = ".js-accordion-body";
  private static readonly AccordionIconSelector = ".js-accordion-icon";

  private showElement(element: JQuery<HTMLElement>): void {
    element.toggleClass("show");
    element.slideToggle(350);
  }

  private hideElement(element: JQuery<HTMLElement>): void {
    element.removeClass("show");
    element.slideUp(350);
  }

  private attachTriggerEvent(element: JQuery<HTMLElement>): void {
    const accordion = this;

    element.on("click", function (e) {
      e.preventDefault();
      var $this = $(this);

      if ($this.next().hasClass("show")) {
        accordion.hideElement($this.next());
      } else {
        accordion.hideElement($this.parent().find(Accordion.AccordionBodySelector));

        accordion.showElement($this.next());
      }

      $this.find(Accordion.AccordionIconSelector).toggleClass("active");
    });
  }

  private renderAccordion(): void {
    if ($(Accordion.AccordionTriggerSelector).length > 0) {
      const accordionTriggerEl = $(Accordion.AccordionTriggerSelector);
      this.attachTriggerEvent(accordionTriggerEl);
    }
  }

  constructor() {
    this.renderAccordion();
  }
}

export default new Accordion;
