import 'slick-carousel';

class Carousel {
    private static readonly MobileWidth = 667;
    private static readonly CarouselSelector = ".js-carousel";

    constructor() {
        $(Carousel.CarouselSelector).each((_, carouselElement) => {
            this.setupCarousel($(carouselElement));
        });
    }

    private GenerateArrowHtml(arrowDirection: string, arrowColor: string, iconClass: string): string {
        return `<i class='carousel-slider__arrow carousel-slider__arrow--${arrowDirection} carousel-slider__arrow--color-${arrowColor} ${iconClass} js-arrow-${arrowDirection}'></i>`;
    }

    private setupCarousel(element: JQuery<HTMLElement>): void {
        if (element.length > 0) {
            let arrowColor = element.data("carousel-button-color");

            element.slick({
                prevArrow: this.GenerateArrowHtml("prev", arrowColor, "fas fa-chevron-left"),
                nextArrow: this.GenerateArrowHtml("next", arrowColor, "fas fa-chevron-right"),
                responsive: [
                    {
                        breakpoint: Carousel.MobileWidth,
                        settings: {
                            arrows: false,
                            slidesToShow: 1,
                            centerMode: true
                        }
                    }
                ]
            });
        }
    }
}

export default new Carousel;
