class ResponsiveImage {
  private static readonly MobileWidth = 667;

  private setImage(element: JQuery<HTMLElement>, toDesktop: boolean): void {
    const desktopImage = this.getDesktopImage(element);
    const mobileImage = this.getMobileImage(element);
    const image = toDesktop ? desktopImage : mobileImage !== "" ? mobileImage : desktopImage;

    element.attr('src', image);

    // Check if we should use a CSS `background-image`, typically for stretching
    if (element.is('.js-use-background-image')) {
      const backgroundImageElement = element.next('.js-background-image');
      if (!backgroundImageElement.length) {
        element.after(`<div class="responsive-image--background-image js-background-image" style="background-image: ${`url('${image}')`}"></div>`);
      } else {
        backgroundImageElement.css('background-image', `url('${image}')`);
      }

      element.css('visibiity', 'hidden');
    }
  }

  private getDesktopImage(element: JQuery<HTMLElement>): string {
    return element.attr('data-desktop-src');
  }

  private getMobileImage(element: JQuery<HTMLElement>): string {
    return element.attr('data-mobile-src');
  }

  private resizeImages(): void {
    $('.js-responsive-image').each((_, node) => {
      const element = $(node);
      const isDesktop = $(window).width() > ResponsiveImage.MobileWidth;

      this.setImage(element, isDesktop);
    });
  }

  constructor() {
    this.resizeImages();

    $(window).smartresize(() => {
      this.resizeImages();
    });
  }
}

export default new ResponsiveImage;
