$(document).on('click', 'a[href^="#"]', function (event) {
  event.preventDefault();
  let target;
  try {
    target = $($(event.currentTarget).attr("href"));
  } catch (err) {
    target = $();
  }

  if (target.length && target !== "#") {
    // add an offset at the top so that we don't cover the anchor target and it shows nicely in the screen display
    const scrollHeight = target.offset().top - 50;
    $('html, body').animate({
      scrollTop: scrollHeight
    }, 500);
  } else {
    if ($(this).hasClass('scrollTo')) {
      $('html, body').animate({
        scrollTop: 0
      }, 500);
    }
  }
});
