class ContentModal {
  private attachListeners(): void {
    $(".js-content-modal").each((_, node) => {
      const contentModalModuleElement = $(node);
      const contentModalModuleContainerElement = contentModalModuleElement.parent();
      const contentModalElement = contentModalModuleElement.children(".js-content-modal__modal");

      contentModalModuleElement.parent().on("click", ".js-content-modal__hook", e => {
        e.stopImmediatePropagation();

        contentModalElement.modal("show");
      });
    });
  }

  constructor() {
    this.attachListeners();
  }
}

export default new ContentModal;
